import React, { useState, useEffect } from "react";

function App() {
  const [list, setList] = useState([]);
  const [listdata, setListdata] = useState([]);

  const [searchValue, setSearchValue] = useState("");
  const [ChangeValue, setChangeValue] = useState("");

  const getApi = async () => {
    let Api = await fetch(
      "https://my-json-server.typicode.com/Ved-X/assignment/orders"
    );
    let apidata = await Api.json();
    setList(apidata);
    setListdata(apidata);
    console.log("apidata", apidata);
  };

  const handleSelect = (event) => {
    // alert(event.target.value);
    setChangeValue(event.target.value);

    var arr = list.filter((item) => {
      if (event.target.value === item.status) {
        return item;
      } else if (event.target.value === "Reset") return item;
    });
    setListdata(arr);
  };
  useEffect(() => {
    getApi();
  }, []);
  return (
    <div style={{ display: "flex" }}>
      <div>
        <nav className="navbar navbar-light bg-light">
          <div className="container-fluid">
            <a className="navbar-brand" style={{ color: "black" }}>
              Customer data table
            </a>
            <form className="d-flex">
              <input
                className="form-control me-5"
                type="search"
                placeholder=" Search by customer name"
                aria-label="Search"
                value={searchValue}
                onChange={(event) => {
                  setSearchValue(event.target.value);
                }}
              />
              <select
                class="form-select"
                aria-label="Default select example"
                onChange={handleSelect}
                // {(event) => setChangeValue(event.target.value)}
                value={ChangeValue}
              >
                <option>Filter by status</option>
                <option value="Delivered">Delivered</option>
                <option value="Completed">Completed</option>
                <option value="Prepared">Prepared</option>
                <option value="Reset">Reset</option>
              </select>
            </form>
          </div>
        </nav>

        <table
          className="table table-striped"
          style={{ border: "1px solid black", width: "100%" }}
        >
          <thead>
            <tr>
              <th scope="col"> ID</th>
              <th scope="col">customer</th>
              <th scope="col">country</th>
              <th scope="col">address</th>

              <th scope="col">product_title</th>
              <th scope="col">product_description</th>
              <th scope="col">date</th>
              <th scope="col">status</th>
            </tr>
          </thead>
          <tbody>
            {listdata
              .filter((user1) => {
                if (searchValue === "") {
                  return user1;
                } else if (
                  user1.customer
                    .toLowerCase()
                    .includes(searchValue.toLowerCase())
                ) {
                  return user1;
                }
              })
              .map((ce) => {
                return (
                  <tr>
                    <th scope="row">{ce.order_id}</th>
                    <td>{ce.customer}</td>
                    <td>{ce.country}</td>
                    <td>{ce.address}</td>
                    <td>{ce.product_title}</td>
                    <td>{ce.product_description}</td>
                    <td>{ce.date}</td>
                    <td>{ce.status}</td>

                    <td></td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default App;
